import store from '@/store'
import config from '@/config'
import logo from '@/log'

import { getToken } from '@/utils/auth'
import errorCode from '@/utils/errorCode'
import { toast, showConfirm, tansParams } from '@/utils/common'

let timeout = 3000000
const baseUrl = config.baseUrl

const request = config => {
  //  token
  const isToken = (config.headers || {}).isToken === false
  config.header = config.header || {}
  if (getToken() && !isToken) {
    config.header['Authorization'] = 'Bearer ' + getToken()
  }
  // getparams
  if (config.params) {
    let url = config.url + '?' + tansParams(config.params)
    url = url.slice(0, -1)
    config.url = url
  }
  return new Promise((resolve, reject) => {
    uni.request({
        method: config.method || 'get',
        timeout: config.timeout ||  timeout,
        url: config.baseUrl || baseUrl + config.url,
        data: config.data,
        header: config.header,
        dataType: 'json'
      }).then(response => {
        let [error, res] = response
        if (error) {
		  logo.error(""+JSON.stringify(config)+""+JSON.stringify(res));
          toast('')
          reject('')
          return
        }
		
        const code = res.data.code || 200
        const msg = errorCode[code] || res.data.msg || res.data.messages|| errorCode['default']
        if (code === 401) {
		  logo.error(""+JSON.stringify(config)+""+JSON.stringify(res));
          showConfirm('?').then(res => {
            if (res.confirm) {
              store.dispatch('LogOut').then(res => {
                uni.reLaunch({ url: 'pages/mine/index' })
              })
            }
          })
          reject('')
        } else if (code === 500) {
		logo.error(""+JSON.stringify(config)+""+JSON.stringify(res));
          toast(msg)
          reject('500')
        } else if (code !== 200 && code != 201) {
		 //logo.info(""+JSON.stringify(res.data)+""JSON.stringify(res.data));
          toast(msg)
          reject(code)
        }
        resolve(res.data)
      })
      .catch(error => {
        let { message } = error
        if (message === 'Network Error') {
		  logo.error(""+JSON.stringify(config)+""+JSON.stringify(res));
          message = ''
        } else if (message.includes('timeout')) {
		  logo.error(""+JSON.stringify(config)+""+JSON.stringify(res));
          message = ''
        } else if (message.includes('Request failed with status code')) {
		 logo.error(""+JSON.stringify(config)+""+JSON.stringify(res));
          message = '' + message.substr(message.length - 3) + ''
        }
        toast(message)
        reject(error)
      })
  })
}

export default request
