export default {
  // 
  msg(content) {
    uni.showToast({
      title: content,
      icon: 'none'
    })
  },
  // 
  msgError(content) {
    uni.showToast({
      title: content,
      icon: 'error'
    })
  },
  // 
  msgSuccess(content) {
    uni.showToast({
      title: content,
      icon: 'success'
    })
  },
  // 
  hideMsg(content) {
    uni.hideToast()
  },
  // 
  alert(content, title) {
    uni.showModal({
      title: title || '',
      content: content,
      showCancel: false
    })
  },
  // 
  confirm(content, title) {
    return new Promise((resolve, reject) => {
      uni.showModal({
        title: title || '',
        content: content,
        cancelText: '',
        confirmText: '',
        success: function(res) {
          if (res.confirm) {
            resolve(res.confirm)
          }
        }
      })
    })
  },
  // 
  showToast(option) {
    if (typeof option === "object") {
      uni.showToast(option)
    } else {
      uni.showToast({
        title: option,
        icon: "none",
        duration: 2500
      })
    }
  },
  // 
  loading(content) {
    uni.showLoading({
      title: content,
      icon: 'none'
    })
  },
  // 
  closeLoading() {
    uni.hideLoading()
  }
}
