export default {
  // 
  reLaunch(url) {
    return uni.reLaunch({
      url: url
    })
  },
  // tabBartabBar
  switchTab(url) {
    return uni.switchTab({
      url: url
    })
  },
  // 
  redirectTo(url) {
    return uni.redirectTo({
      url: url
    })
  },
  // 
  navigateTo(url) {
    return uni.navigateTo({
      url: url
    })
  },
  // 
  navigateBack() {
    return uni.navigateBack()
  }
}
